export default {
	data() {
		return {
			userInfo: {},
			loginInfo: {},
			code: '',
			session: '',
			logined:false,//是否登录
		}
	},
	methods: {
		goNext(){
			// 直接进入下一页
			uni.navigateTo({
				url:'/pages/consult/consult'
			})
		},
		//获取用户授权回调
		getInfoCallback(e) {
			console.log('e',e);
			if (e.detail && e.detail.errMsg == "getUserInfo:ok") {
				let userInfo=e.detail.userInfo;
				this.userInfo = userInfo;
				
				this.goConsult();
			} else {
				this.$notice.msg("您已拒绝了授权！");
			}
		},
		//跳转我的定制页
		goConsult() {
			let userInfo=this.userInfo;
			let infos = {
				city:userInfo["city"],
				country:userInfo["country"],
				ico:userInfo["avatarUrl"],
				nick:userInfo["nickName"],
				province:userInfo["province"],
				sex:userInfo["gender"],
			};
			this.userAuth(infos);
		},
		//userAuth post
		userAuth(param) {
			this.$http({
				url: '/api/auth',
				data: param
			}).then(res => {
				console.log("=成功!", res);
				uni.setStorageSync('userInfo', param);
				uni.navigateTo({
					url:'/pages/consult/consult'
				})
			}).catch(err => {
				console.log('=err',err);
			})
		},
		//登录 
		goLogin(code) {
			this.$http({
				url: '/api/login',
				method: "GET",
				data: {
					code: code
				}
			}).then(res => {
				console.log("成功!", res);
				this.session = res.data.session;
				uni.setStorageSync('session',this.session);
				uni.setStorageSync('userInfo', res.data);
				this.logined=true;
				uni.hideLoading();
			}).catch(err => {
				console.log('==err', err);
				this.session = err.data.session;
				uni.setStorageSync('session',this.session);
				uni.hideLoading();
			})
		},
		//获取微信code
		getWxCode() {
			uni.login({
			provider: 'weixin',
			success: (res)=> {
				console.log(res);
				
				if (res.code) {
					this.code = res.code; 
					this.goLogin(res.code);//获取用户信息
				} else {
					this.$notice.alert('获取微信code失败，异常原因：'+res.errMsg)
				}
			}
			});
		},
	},
	mounted() {
		uni.showLoading({
			title: '加载中'
		});
		this.getWxCode();
	},
	onShow(){
		this.$tools.getRecentVersion();
	}
}
